<template>Â 
  <div class="app-container">
    <el-card class="filter-container"
             shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>ç­éæç´¢</span>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true"
                 :model="listQuery"
                 size="small">
          <el-form-item label-width="140px"
                        label="ç¨æ·åç§°ï¼">
            <el-input v-model="listQuery.username"
                      class="input-width"
                      placeholder="ç¨æ·åç§°"
                      clearable></el-input>
          </el-form-item>
          <el-form-item label-width="88px"
                        label="ææºå·ï¼">
            <el-input v-model="listQuery.mobile"
                      class="input-width"
                      placeholder="ææºå·"
                      clearable></el-input>
          </el-form-item>

          <el-button @click="handleResetSearch"
                     type="primary"
                     size="small"
                     icon="el-icon-delete">éç½®</el-button>
          <el-button @click="search"
                     type="primary"
                     icon="el-icon-search"
                     size="small">æ¥è¯¢</el-button>
        </el-form>
      </div>
    </el-card>
    <el-card class="operate-container"
             shadow="never">
      <i class="el-icon-tickets"></i>
      <span>æ°æ®åè¡¨</span>
      <el-button type="primary"
                 icon="el-icon-plus"
                 style="float:right;margin-bottom:18px"
                 @click="handleAdd()">æ·»å ç¨æ·</el-button>
    </el-card>
    <div class="table-container">
      <el-table ref="userTable"
                :data="list"
                style="width: 100%;"
                element-loading-text="æ¼å½å è½½ä¸­"
                element-loading-spinner="el-icon-loading"
                element-loading-background="#fff"
                v-loading="listLoading"
                :row-style="{height:'10px'}"
                border>
        <el-table-column type="index"
                         label="åºå·"
                         width="50"
                         show-overflow-tooltip
                         align="center"></el-table-column>
        <el-table-column prop="username"
                         min-width="140"
                         label="ç¨æ·åç§°"
                         show-overflow-tooltip
                         align="center"></el-table-column>
        <el-table-column prop="mobile"
                         min-width="130"
                         label="ææºå·"
                         show-overflow-tooltip
                         align="center"></el-table-column>
        <el-table-column prop="email"
                         min-width="80"
                         label="é®ç®±"
                         show-overflow-tooltip
                         align="center"></el-table-column>
        <el-table-column prop="user_roles"
                         min-width="80"
                         label="ç¨æ·ç±»å"
                         show-overflow-tooltip
                         align="center">
          <!-- <template v-slot="scope">
          <span>{{scope.type | typeToString}}</span>
        </template> -->
          <template slot-scope="scope">{{scope.row.user_roles | roleToString}}</template>
        </el-table-column>
        <el-table-column prop="create_time"
                         min-width="133"
                         label="ç¨æ·åå»ºæ¶é´"
                         show-overflow-tooltip
                         align="center"></el-table-column>
        <el-table-column label="æä½"
                         width="180"
                         align="center">
          <template slot-scope="scope">
            <el-button type="text"
                       icon="el-icon-edit"
                       @click="handleUpdate(scope.$index, scope.row)">
              ç¼è¾
            </el-button>
            <el-button type="text"
                       icon="el-icon-delete"
                       class="red"
                       @click="handleDelete(scope.$index, scope.row)">å é¤
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination-container">
      <el-pagination background
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     layout="total, sizes,prev, pager, next,jumper"
                     :current-page.sync="listQuery.pageNum"
                     :page-size="listQuery.pageSize"
                     :page-sizes="[10,50,100]"
                     :total="total">
      </el-pagination>
    </div>
    <el-dialog :title="`ç¨æ·${isEdit?'ç¼è¾':'æ°å¢'}`"
               :visible.sync="dialogVisible"
               center
               @close="cancelEdit"
               width="40%">
      <el-form :model="userPromotion"
               :rules="rules"
               ref="userPromotion"
               label-width="150px"
               size="small">
        <el-form-item prop="username"
                      label="ç¨æ·åï¼">
          <el-input v-model="userPromotion.username"
                    style="width: 250px"></el-input>
        </el-form-item>
        <el-form-item 
                      label="å¯ç ï¼">

          <el-input name="password"
                    :type="pwdType"
                    v-model="userPromotion.password"
                    autoComplete="on"
                    style="width: 250px"
                    placeholder="è¯·è¾å¥å¯ç ">
            <span slot="prefix">
              <svg-icon icon-class="password"
                        class="color-main"></svg-icon>
            </span>
            <span slot="suffix"
                  @click="showPwd">
              <svg-icon icon-class="eye"
                        class="color-main"></svg-icon>
            </span>
          </el-input>

          <!-- <el-input v-model="userPromotion.password" show-password = "true" autocomplete = "on" type = "password"
                    style="width: 250px"></el-input> -->
        </el-form-item>

        <el-form-item prop="mobile"
                      label="ææºå·ï¼">
          <el-input v-model="userPromotion.mobile"
                    style="width: 250px"></el-input>
        </el-form-item>
        <el-form-item label="é®ç®±ï¼">
          <el-input v-model="userPromotion.email"
                    style="width: 250px"></el-input>
        </el-form-item>
        <el-form-item prop="user_roles"
                      label="ç¨æ·ç±»åï¼">
          <!-- <el-input v-model="userPromotion.type"
                    style="width: 250px"> -->
          <el-select v-model="userPromotion.user_roles"
                     clearable
                     placeholder="è¯·éæ©ç¨æ·ç±»å "
                     style="width: 250px">
            <el-option v-for="item in userOpt"
                       :key="item.type"
                       :label="item.label"
                       :value="item.type"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div style="text-align: center;">
        <el-button @click="dialogVisible=false">å æ¶</el-button>
        <el-button type="primary"
                   @click="handleDialogConfirm()">ç¡® å®</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { getUserList, createUser, updataUser, delUser } from '@/api/api'
import { formatDate } from '@/utils/date'
import { Message, MessageBox } from 'element-ui'

const defaultListQuery = {
  pageNum: 1,
  pageSize: 10,
  username: '',
  mobile: null
}
const defaultuserPromotion = {
  id: null,
  username: '',
  mobile: null,
  password: null,
  email: null,
  user_roles: null
}

const userOpt = [
  { type: 1, label: 'å¼åç¨æ·' },
  { type: 2, label: 'æµè¯ç¨æ·' }
]

export default {
  name: 'userPromotionList',
  data() {
    return {
      listQuery: Object.assign({}, defaultListQuery),
      list: null,
      pwdType: 'password',
      total: null,
      searchs: {}, //æç»ç¡®å®çæç´¢ä¿¡æ¯ï¼éè¦å¤çè¿
      listLoading: false,
      dialogVisible: false,
      userPromotion: Object.assign({}, defaultuserPromotion),
      isEdit: false, // trueç¼è¾ï¼falseæ°å¢
      userOpt: userOpt,
      rules: {
        username: [
          { required: true, message: 'ç¨æ·åç§°ä¸è½ä¸ºç©º', trigger: 'blur' },
          { max: 10, message: 'æå¤å¯è¾å¥30ä¸ªå­ç¬¦', trigger: 'blur' }
        ],
        mobile: [{ required: true, message: 'ææºå·å¿è¾', trigger: 'blur' },
        { min: 11,max:11, message: 'è¾å¥11ä¸ªæ°å­', trigger: 'blur' }],
        user_roles: [
          { required: true, message: 'ç¨æ·ç±»åå¿è¾', trigger: 'blur' }
        ],
        password: [{ required: true, message: 'å¯ç å¿è¾', trigger: 'blur' },
        { min: 6, message: 'æå°è¾å¥6ä¸ªå­ç¬¦', trigger: 'blur' }]
      }
    }
  },
  created() {
    this.listLoading = true
    getUserList(this.listQuery).then(response => {
      this.listLoading = false
      this.list = response.data
      this.total = response.count
    })
  },
  filters: {
    formatDate(time) {
      if (time == null || time === '') {
        return 'N/A'
      }
      let date = new Date(time)
      return formatDate(date, 'yyyy-MM-dd')
    },

    roleToString(val) {
      switch (val) {
        case 1:
          return 'å¼åç¨æ·'
          break
        case 2:
          return 'æµè¯ç¨æ·'
          break
        default:
          return 'æµè¯ç¨æ·'
      }
    }
  },
  methods: {
    getData() {
      this.listLoading = true
      getUserList({
        pageNum: this.listQuery.pageNum,
        pageSize: this.listQuery.pageSize,
        username: this.listQuery.username,
        mobile: this.listQuery.mobile
      }).then(response => {
        this.listLoading = false
        this.list = response.data
        this.total = response.count
      })
    },
    showPwd() {
      if (this.pwdType === 'password') {
        this.pwdType = ''
      } else {
        this.pwdType = 'password'
      }
    },

    // éç½®
    handleResetSearch() {
      this.listQuery = Object.assign({}, defaultListQuery)
    },

    // æç´¢
    search() {
      this.listQuery.pageNum = 1
      this.searchs = this.listQuery
      //   console.log(this.listQuery)
      this.getData()
    },
    // åé¡µç¸å³æä½
    handleSizeChange(val) {
      this.listQuery.pageNum = 1
      this.listQuery.pageSize = val
      this.getData()
    },
    handleCurrentChange(val) {
      this.listQuery.pageNum = val
      this.getData()
    },

    handleAdd() {
      this.dialogVisible = true
      this.isEdit = false
      this.flag = false
      this.userPromotion = Object.assign({}, defaultuserPromotion)
    },

    handleStatusChange(index, row) {
      this.$confirm('æ¯å¦è¦ä¿®æ¹è¯¥ç¶æ?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®',
        cancelButtonText: 'åæ¶',
        type: 'warning'
      })
        .then(() => {
          updateStatus(row.id, { status: row.status }).then(response => {
            this.$message({
              type: 'success',
              message: 'ä¿®æ¹æå!'
            })
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: 'åæ¶ä¿®æ¹'
          })
          this.getData()
        })
    },
    handleDelete(index, row) {
      this.$confirm('æ¯å¦è¦å é¤è¯¥ç¨æ·?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®',
        cancelButtonText: 'åæ¶',
        type: 'warning'
      }).then(() => {
        delUser(row.id).then(response => {
          this.$message({
            type: 'success',
            message: 'å é¤æå!',
            center: true
          })
          this.getData()
        })
      })
    },
    handleUpdate(index, row) {
      this.dialogVisible = true
      this.isEdit = true
      this.flag = true
      this.userPromotion = Object.assign({}, row)
    },

    // æ¸ç©ºéªè¯ä¿¡æ¯
    cancelEdit() {
      this.$refs.userPromotion.clearValidate()
    },

    handleDialogConfirm() {
      if (this.isEdit) {
        this.$refs.userPromotion.validate(valid => {
          if (valid) {
            updataUser(this.userPromotion.id, this.userPromotion).then(
              response => {
                if ('id' in response) {
                  this.$message({
                    message: 'ä¿®æ¹æåï¼',
                    type: 'success',
                    center: true
                  })
                  this.dialogVisible = false
                  this.getData()
                } else {
                  Message({
                    message: response.data,
                    type: 'error',
                    duration: 3 * 1000
                  })
                }
              }
            )
          } else {
            return false
          }
        })
      } else {
        this.$refs.userPromotion.validate(valid => {
          if (valid) {
            createUser(this.userPromotion).then(response => {
              if ('id' in response) {
                this.$message({
                  message: 'æ·»å æåï¼',
                  type: 'success',
                  center: true
                })
                this.dialogVisible = false
                this.getData()
              } else {
                Message({
                  message: response.data,
                  type: 'error',
                  duration: 3 * 1000
                })
              }
            })
          } else {
            return false
          }
        })
      }
    }
  }
}
</script>
<style></style>





